<template>
    <div ref="sunchart" class="echarts" style="width:800px;height:500px;margin:0 auto" v-show="show"></div>
</template>

<script>
    import { animate } from "@/function/animate.js";
    import * as echarts from "echarts/core";
    import { TitleComponent, LegendComponent, TooltipComponent } from "echarts/components";
    import { PieChart } from "echarts/charts";
    import { SVGRenderer } from "echarts/renderers";
    export default {
        props: ["Year", "Medal", "Season"],
        async mounted() {
            echarts.use([TitleComponent, LegendComponent, TooltipComponent, PieChart, SVGRenderer]);
            this.dataset = await fetch("./每年奖牌趋势.json").then((res) => res.json());

            this.myChart = echarts.init(this.$refs.sunchart);
            this.reDraw();
        },
        data() {
            return {
                show: true,
                dataset: {},
                myChart: {},
                rendered: false,
                option: {
                    legend: {
                        top: "5%",
                        left: "center",
                    },
                    tooltip: {
                        trigger: "item",
                        formatter: "<b>{b}</b><br\> {c} 枚 | 占比 {d} %",
                    },
                    title: {
                        text: "",
                        top: "57%",
                        left: "center",
                    },

                    series: [],
                },
            };
        },
        watch: {
            $route: function() {
                try {
                    this.Medal = this.$route.params.Medal;
                    this.Year = this.$route.params.Year;
                    this.Season = this.$route.params.Season;
                    this.show = true;
                    this.reDraw();
                } catch (e) {
                    this.show = false;
                }
            },
        },
        methods: {
            sort(data) {
                let values = Object.values(data);
                const avg = Math.floor(values.reduce((a, b) => a + b, 0) / values.length) * 1.3;
                return Object.entries(data).reduce(
                    (arr, [name, value]) => {
                        if (value > avg) {
                            arr.push({ name, value });
                        } else {
                            arr[0].value += value;
                        }

                        return arr;
                    },
                    [{ name: "其他国家", value: 0 }]
                );
            },
            reDraw() {
                console.log(this.Season, this.Year, this.Medal);
                const target = this.dataset[this.Season][this.Year][this.Medal];
                let Medal;
                switch (this.Medal) {
                    case "Gold":
                        Medal = "金牌";
                        break;
                    case "Silver":
                        Medal = "银牌";
                        break;
                    case "Bronze":
                        Medal = "铜牌";
                        break;
                }
                this.option.title.text = this.Year + Medal;
                const that = this;
                this.option.series = [target].map((data, index) => ({
                    type: "pie",
                    radius: ["40%", "70%"],
                    center: ["50%", "60%"],
                    itemStyle: {
                        borderRadius: 8,
                    },
                    emphasis: {
                        label: {
                            show: true,
                            fontSize: "20",
                            fontWeight: "bold",
                        },
                    },

                    data: that.sort(data),
                }));
                this.myChart.setOption(this.option);
                console.log(this.option.series);
                animate(this.$refs.sunchart, "fadeIn");
            },
        },
    };
</script>
<style lang="less" scoped>
    svg {
        text {
            transition: 1s;
        }
    }
</style>
